* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: 'Courier New', Courier, monospace;
  line-height: 1.4;
  color: #444;
  display: grid;
  justify-content: center;
  align-content: center;
}

img {
  width: 15em;
  height: 10em;
}
.App {
  display: grid;
  min-width: 350px;
  grid-template-areas:
    'header header'
    'title  title'
    'break  session'
    'clock  clock';
  grid-template-rows: 10em 2fr 2fr auto;
  grid-template-columns: repeat(2, 1fr);
  text-align: center;
}
.header {
  grid-area: header;
}
.title {
  grid-area: title;
}
.title h1 {
  font-family: 'Lobster', cursive;
  font-size: 3em;
}
.break {
  grid-area: break;
  justify-self: self-start;
}
.session {
  grid-area: session;
  justify-self: self-end;
}
.clock {
  grid-area: clock;
  margin-top: 1em;
  justify-self: center;
}
.timer {
  border: 2px solid #ccc;
  border-radius: 50px;
  width: 18em;
  height: 9em;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.timer p {
  font-size: 4em;
}
#timer-label {
  font-size: 1.5em;
  text-transform: capitalize;
}
span {
  display: inline-block;
}
#break-decrement,
#session-decrement {
  border-left: 0.5em solid transparent;
  border-right: 0.5em solid transparent;
  border-top: 1em solid #cc2e51;
  cursor: pointer;
}
#break-increment,
#session-increment {
  border-left: 0.5em solid transparent;
  border-right: 0.5em solid transparent;
  border-bottom: 1em solid #222;
  cursor: pointer;
}
.break-footer,
.session-footer {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.btn-container i {
  border: 1px solid #ccc;
  padding: 0.5em;
  margin: 0.5em;
  cursor: pointer;
}

.hide {
  display: none;
}

